.app-bg {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  text-align: center;
  background-image: linear-gradient(to bottom right, #3a5673, #aaf1f5);
  color: white;
  justify-content: flex-start;
  align-items: center;
}

.head {
  color: #f6d4d2;
  font-size: 42px;
  font-weight: bolder;
  text-align: center;
  margin: 50px;
  background-color: #131a28;
  padding: 15px 25px;
  border-radius: 8px;
}

.task-container {
  display: flex;
  max-width: 80vh;
  height: 100%;
  flex-direction: column;
  text-align: center;
  background-color: #131a28;
  color: white;
  border-radius: 8px;
  justify-content: flex-start;
  align-items: center;
  padding: 30px 20px;
  margin: 0 auto 20px;
  min-height: 205px;
}

.inputText {
  border: #f6d4d2 2px solid;
  outline: none;
  border-radius: 8px;
  width: 310px;
  font-size: 18px;
  padding: 10px 15px;
  margin: 0 15px 15px 15px;
  font-weight: bold;
}

button {
  background-color: #f6d4d2;
  outline: none;
  border: none;
  color: #131a28;
  font-size: 17px;
  font-weight: bolder;
  padding: 10px 15px;
  border-radius: 8px;
  text-align: center;
  margin: 0 15px 15px 15px;
  cursor: pointer;
}

.no-task {
  color: #f6d4d2;
  font-size: 17px;
  font-weight: bolder;
  text-align: center;
  margin: 50px;
}

.list-items {
  background-color: #f6d4d2;
  border: none;
  border-radius: 8px;
  width: 450px;
  font-size: 18px;
  padding: 10px 15px;
  margin: 15px 0;
  color: #131a28;
  font-weight: bold;
  text-align: start;
  align-items: center;
  cursor: pointer;
}

.delete-icon {
  height: 26px;
  cursor: pointer;
}

.row {
  display: flex;
  flex-direction: row;
}

.col {
  display: flex;
  flex-direction: column;
}

.jc-space-between {
  justify-content: space-between;
}

.footer {
  color: #131a28;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  margin: 10px;
  padding: 15px 25px;
}

.task-done {
  text-decoration: line-through;
  color: #56c8ce;
}
